.snip1344 {
    font-family: 'Open Sans', Arial, sans-serif;
    position: relative;
    overflow: hidden;
    margin: 10px;
    width: 300px;
    height: 350px;
    color: #ffffff;
    text-align: center;
    line-height: 1.4em;
    background-color: #141414;

    border-radius:10px;
    cursor: pointer;

  }
  .snip1344 * {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    -webkit-transition: all 0.25s ease;
    transition: all 0.25s ease;
  }
  .snip1344 .background {
    width: 100%;
    vertical-align: top;
    opacity: 0.2;
    -webkit-filter: grayscale(100%) blur(10px);
    filter: grayscale(100%) blur(10px);
    -webkit-transition: all 2s ease;
    transition: all 2s ease;
  }
  .snip1344 figcaption {
    width: 100%;
    padding: 15px 25px;
    position: absolute;
    left: 0;
    top: 50%;
  }
  .snip1344 .profile {
    border-radius: 50%;
    position: absolute;
    bottom: 50%;
    left: 50%;
    width: 100px;
    height: 100px;
    opacity: 1;
    box-shadow: 3px 3px 20px rgba(0, 0, 0, 0.5);
    border: 2px solid rgba(255, 255, 255, 0.5);
    -webkit-transform: translate(-50%, 0%);
    transform: translate(-50%, 0%);
  }
  .snip1344 h3 {
    margin: 25px 0 5px;
    font-weight: 400;
  }
  .snip1344 h3 span {
    display: block;
    font-size: 0.6em;
    color: #f39c12;
    margin-top: 10px;
    opacity: 0.75;
  }
  .snip1344 i {
    padding: 10px 5px;
    display: inline-block;
    font-size: 32px;
    color: #ffffff;
    text-align: center;
    opacity: 0.65;
  }
  .snip1344 a {
    text-decoration: none;
  }
  .snip1344 i:hover {
    opacity: 1;
    -webkit-transition: all 0.35s ease;
    transition: all 0.35s ease;
  }
  .snip1344:hover .background,
  .snip1344.hover .background {
    -webkit-transform: scale(1.3);
    transform: scale(1.3);
  }