  .button-container {
    display: flex;
    margin: 60px auto;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .button.customButton {
    display: flex;
    overflow: hidden;
    margin: 50px 10px 15px 10px;
    padding: 12px 12px;
    cursor: pointer;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    
    text-align: center;
    white-space: nowrap;
    text-decoration: none !important;
    text-transform: none;
    text-transform: capitalize;
    color: #fff;
    border: 0 none;
    border-radius: 36px;
    border-radius: var(--borderRadius);
    font-size: 13px;
    font-weight: 500;
    line-height: 1.3;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    justify-content: center;
    align-items: center;
    flex: 0 0 160px;
    box-shadow: 2px 5px 10px #000;
  }
  .button.customButton:hover {
    transition: all 150ms linear;
    opacity: 0.85;
    transform: translate3d(0px, 2px, 0px);
    box-shadow: 2px 3px 10px #000;
  }
  .button.customButton:active {
    transition: all 150ms linear;
    opacity: 0.75;
  }
  .button.customButton:focus {
    outline: 1px dotted #959595;
    outline-offset: -4px;
  }
  
  
  .button.customButton.-dark {
    color: #ffffff;
    color: var(--color-snow);
    background: #161616;
    background: var(--color-dark);
  }
  .button.customButton.-dark:focus {
    outline: 1px dotted white;
    outline-offset: -4px;
  }
